import React, { Component } from "react";
import WIPDash from "./Main.controller";
import $ from "jquery";
import DataTable from "datatables";
import store from "./globalStore";

let CanvasJS = require("./canvasjs.min");

CanvasJS = CanvasJS.Chart ? CanvasJS : window.CanvasJS;

//canvasJS
class CanvasJSChart extends Component {
    static _cjsContainerId = 0;

    constructor(props) {
        super(props);
        this.options = props.options ? props.options : {};
        this.containerProps = props.containerProps
            ? props.containerProps
            : {
                  width: "100%",
                  position: "relative"
              };
        this.containerProps.height =
            props.containerProps && props.containerProps.height
                ? props.containerProps.height
                : this.options.height
                ? this.options.height + "px"
                : "400px";
        this.chartContainerId =
            "canvasjs-react-chart-container-" + CanvasJSChart._cjsContainerId++;
        this.data = props.options.data;
        this.state = {
            params: { "Param.1": localStorage.getItem("CurrentStation") }
        };
    }
    componentDidMount() {
        this.chart = new CanvasJS.Chart(this.chartContainerId, this.options);
        this.chart.render();

        if (this.props.onRef) this.props.onRef(this.chart);

        //store.subscribe(() => {

        if (store.getState().begin) {
            new WIPDash().updateDom(
                1000,
                this.state.params,
                "dataExtended",
                "graphData",
                () => {
                    const oModel = new WIPDash().getData("graphData"),
                        arr = oModel.map((e, i) => {
                            return {
                                label: e.STATION_DESC,
                                y: e["En cours"],
                                x: i
                            };
                        }),
                        nextProps = {
                            options: {
                                animationEnabled: true,
                                exportEnabled: true,
                                theme: "light2",
                               // height: 850,
                                width: 1775,
                                axisX: {
                                    interval: 1
                                },
                                title: {
                                    text: "PCs",
                                    fontWeight: "bold",
                                    fontColor: "#008B8B",
                                    fontfamily: "Source Sans Pro"
                                },
                                data: [
                                    {
                                        type: "column",
                                        showInLegend: false,
                                        legendText: "Station Description",
                                        dataPoints: arr
                                    }
                                ]
                            }
                        };

                    this.shouldComponentUpdate(nextProps, true);
                }
            );
        }
        //  });
    }
    shouldComponentUpdate(nextProps, nextState) {
        const condition =
            JSON.stringify(nextProps.options.data[0].dataPoints) !==
            JSON.stringify(this.chart.options.data[0].dataPoints);

        condition && this.componentDidUpdate(nextProps);

        return condition;
    }
    componentDidUpdate(nextProps) {
        this.chart.options = nextProps.options;
        this.chart.render();
    }
    componentWillUnmount() {
        this.chart.destroy();
        if (this.props.onRef) this.props.onRef(undefined);
    }
    render() {
        return <div id={this.chartContainerId} style={this.containerProps} />;
    }
}
//DataTables with hooks
class Table extends Component {
    constructor(props) {
        super(props);
        this.columns = props.columns;
        this.data = props.data;
        this.tableId = props.id;
        this.tableClasses = props.className;
        this.table = null;
        this.load = true;
        this.state = {
            loading: true,
            iconStyle: "",
            style: "",
            cursorPos: {},
            data: [],
            params: {
                "Param.1": localStorage.getItem("CurrentStation")
            }
        };
    }
    componentDidMount() {
        this.table = $(this.refs.main).DataTable({
            dom: '<"data-table-wrapper"tip>',
            data: this.data,
            columns: this.columns,
            paginate: false,
            ordering: false,
            bAutoWidth: false,
            bDestroy: true,
            responsive: true,
            bInfo: false,
            processing: true,
            serverSide: true,
            ajax: (data, callback, settings) => {
                callback({
                    data:
                        localStorage.getItem("tableData") &&
                        new WIPDash().getData("tableData")
                });
            },
            drawCallback: settings => {
                // if (store.getState().begin) {

                new WIPDash().updateDom(
                    50000,
                    this.state.params,
                    "dataSum",
                    "tableData",
                    () => {
                        let newData = new WIPDash().getData("tableData");
                        this.load && this.shouldComponentUpdate(newData);
                    }
                );
                // }
            }
        });
    }
    shouldComponentUpdate(newData) {
        this.load = false;
        if (newData.length !== this.data.length) {
            this.componentDidUpdate(newData);
        } else {
            this.updateRows(newData);
        }
        return false;
    }
    componentDidUpdate(newData) {
        const table = $(this.refs.main).DataTable();
        table.clear();
        table.rows.add(newData);
        this.data = newData;
        this.load = true;
        table.draw();
    }
    updateRows(newData) {
        const table = $(this.refs.main).DataTable();
        let dataChanged = false;
        this.load = true;
        table.rows().every(e => {
            const oldData = this.data[e];
            if (JSON.stringify(newData[e]) === JSON.stringify(oldData)) {
                return;
            } else {
                dataChanged = true;
                this.data[e] = newData[e];
            }
            return true;
        });

        if (dataChanged) {
            table.draw();
        }
    }
    componentWillUnmount() {
        $(".data-table-wrapper")
            .find("table")
            .DataTable()
            .destroy(true);
    }
    render() {
        return React.createElement("table", {
            id: this.tableId,
            className: this.tableClasses,
            ref: "main"
        });
        //	return ( <div> <table ref={this.refs}/></div>);
    }
}

export { CanvasJSChart, Table };
