import React, { Component } from "react";
import logo from "../public/SplashScreen.png";
import $ from "jquery";
import { Navbar, Nav, Container } from "react-bootstrap";
import { Route, Switch, Redirect, NavLink } from "react-router-dom";
import Home from "./home.view";
import Info from "./info.view";
import "./App.scss";
import WIPDash from "./Main.controller";
import { hot } from "react-hot-loader/root";

import store from "./globalStore";
import { RefreshAction } from "./actions";
import axios from "axios";

class App extends Component {
    constructor(props) {
        super(props);
        this.onSelect = this.onSelect.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
        this.changeCurrent = this.changeCurrent.bind(this);
        this.onClosed = this.onClosed.bind(this);
        this.state = {
            preference: "",
            landingData: {}
        };
    }
    componentDidMount() {
        new Promise(resolve => {
            setTimeout(() => {
                resolve(
                    $("#splashScreen").fadeOut(5000, function() {
                        $(this).css({
                            opacity: "1",
                            display: "none"
                        });
                    })
                );
            }, 1000);
        })
            .then(() => {
                $("#main_content").css("display", "block");
            })
            .then(() => {
                /* axios
          .get("dataExtended.json" 
            //"/XMII/Illuminator?service=OData&mode=FixedQuery&QueryTemplate=Sandbox_DEV/SandboxOO/WIPDashboard/Query/selectDataExtended&content-type=text/json&Param.1=%25"
          )
          .then(response => {*/

                this.setState({
                    landingData: /*response.data*/ JSON.parse(
                        localStorage.graphData
                    ).Rowsets.Rowset[0].Row.map((e, i) => {
                        return {
                            label: e.STATION_DESC,
                            y: e["En cours"],
                            x: i
                        };
                    })
                });

                // });
            });
    }
    onSelect(oEvent) {
        this.setState({
            option: oEvent.target.value
        });
    }
    onSubmit() {
        const selected = $("#inlineFormCustomSelect option:selected").text();
        this.setState({
            preference: selected.trim()
        });
        var page = window.location.href;
        localStorage.setItem(
            "CurrentStation",
            $("#inlineFormCustomSelect option:selected")
                .text()
                .trim()
        );
         store.dispatch(RefreshAction(true));
        this.forceUpdate();
    }
    onClosed(oEvent) {
        $("#splashScreen").css({ display: "flex", "z-index": 2000 });
        new Promise(resolve => {
            setTimeout(() => {
                resolve(
                    $("#splashScreen").fadeOut(2000, function() {
                        $(this).css({
                            opacity: "0",
                            display: "none"
                        });
                    })
                );
            }, 2000);
        }).then(() => {
            window.close();
        });
    }
    changeCurrent() {
        localStorage.removeItem("CurrentStation");
         store.dispatch(RefreshAction(false));
          this.forceUpdate();
    }
    render() {
        let mainContent;
        let btnID = !localStorage.CurrentStation ? "initBtn" : "main_btn"; //redundant

        if (
            !localStorage.CurrentStation &&
            Object.entries(this.state.landingData).length
        ) {
            store.dispatch(RefreshAction(false));

            mainContent = (
                <div
                    className="modal fade"
                    id="exampleModalCenter"
                    tabIndex="-1"
                    role="dialog"
                    aria-labelledby="exampleModalCenterTitle"
                    aria-hidden="true"
                >
                    <div
                        className="modal-dialog modal-dialog-centered"
                        role="document"
                    >
                        <div className="modal-content">
                            <div className="modal-header">
                                <h5
                                    className="modal-title"
                                    id="exampleModalLongTitle"
                                >
                                    Please Select Default Station
                                </h5>
                                <button
                                    type="button"
                                    className="close"
                                    data-dismiss="modal"
                                    aria-label="Close"
                                    onClick={this.onClosed}
                                >
                                    <span aria-hidden="true">&times;</span>
                                </button>
                            </div>
                            <div className="modal-body">
                                <div className="custom-control custom-checkbox mr-sm-2">
                                    <form
                                        className="changeCurr"
                                        id="selectForm"
                                    >
                                        <div className="align-items-center">
                                            <div className="col-auto my-1">
                                                <label
                                                    className="mr-sm-2 sr-only"
                                                    htmlFor="inlineFormCustomSelect"
                                                >
                                                    Preference{" "}
                                                </label>{" "}
                                                <select
                                                    ref="formRef"
                                                    className="custom-select mr-sm-2"
                                                    id="inlineFormCustomSelect"
                                                    onChange={this.onSelect}
                                                >
                                                    {" "}
                                                    {this.state.landingData.map(
                                                        (option, index) => {
                                                            return (
                                                                <option
                                                                    key={index}
                                                                    value={
                                                                        option.label
                                                                    }
                                                                >
                                                                    {" "}
                                                                    {
                                                                        option.label
                                                                    }{" "}
                                                                </option>
                                                            );
                                                        }
                                                    )}{" "}
                                                </select>{" "}
                                            </div>{" "}
                                            <div className="col-auto my-1">
                                                <button
                                                    id={btnID}
                                                    className="submission"
                                                    type="submit"
                                                    className="btn btn-primary"
                                                    onClick={this.onSubmit}
                                                >
                                                    Submit{" "}
                                                </button>{" "}
                                            </div>{" "}
                                        </div>{" "}
                                    </form>
                                </div>
                            </div>
                            <div className="modal-footer">
                                <button
                                    onClick={this.onClosed}
                                    type="button"
                                    className="btn btn-secondary"
                                    data-dismiss="modal"
                                >
                                    Close
                                </button>
                                <button
                                    type="button"
                                    className="btn btn-primary"
                                    onClick={this.onSubmit}
                                >
                                    Submit
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            );
        } else {
            store.dispatch(RefreshAction(true));

            mainContent = (
                <div id="main_content">
                    <Navbar
                        sticky="top"
                        collapseOnSelect
                        expand="lg"
                        className="navbar navbar-light"
                        style={{ backgroundColor: "#e5e5e5" }}
                    >
                        <Nav defaultActiveKey="/home">
                            <div role="tablist">
                                <NavLink
                                    to="/home"
                                    id="nav-home-tab"
                                    className="nav-item nav-tabs active"
                                    role="tab"
                                    aria-controls="home"
                                >
                                    Home
                                </NavLink>
                                <NavLink
                                    to="/info"
                                    id="nav-info-tab"
                                    className="nav-item nav-tabs"
                                    role="tab"
                                    aria-controls="info"
                                >
                                    Information
                                </NavLink>
                            </div>
                        </Nav>
                    </Navbar>

                    <Container>
                        <Switch>
                            <Route
                                exact
                                strict
                                path="/home"
                                component={Home}
                                className="tab-pane fade show active"
                            />
                            <Route exact strict path="/info" component={Info} />
                            <Redirect exact from="/" to="home" />
                        </Switch>
                    </Container>

                    <div className="card-footer text-muted">
                        <div id="footerText">© Etel 2019</div>
                        <div id="formDiv">
                            <button onClick={this.changeCurrent}>
                                {" "}
                                Change Configuration{" "}
                            </button>
                        </div>
                    </div>
                </div>
            );
        }

        return (
            <div className="App" style={{ fontFamily: "Source Sans Pro" }}>
                <div id="splashScreen">
                    <img id="pic" alt="splash" src={logo} />
                </div>
                {mainContent}
            </div>
        );
    }
}
export default hot(App);
