import DeltaTimer from "./BaseController";
import $ from "jquery";
import config from "./config.json";
import store from "./globalStore";
import data from "./dataExtended.json";
import data1 from "./dataSum.json";

export default class WIPDash extends DeltaTimer {
	constructor(
	render,
	interval,
	requestData,
	getConfig,
	init,
	updateDom,
	getData
	) {
		super(render, interval);
		this.ajax = requestData;
		this.config = getConfig;
		this._init = init;
		this.DOM = updateDom;
		this._getData = getData;
	}
	get getConfig() {
		return new Promise((resolve, reject) => {
			resolve(config);
			reject(new Error('fail'));
		});
	}
	updateDom(rate, params, modelName, storageID, condition) {
		new DeltaTimer(() => {
			new Promise((resolve, reject) =>{
				resolve(this.requestData(params, modelName, storageID));
			reject(new Error('fail' + modelName));}
			).then(val => condition());
		}, rate).start();
	}
	requestData(params, modelName, storageID) {
		localStorage.setItem("graphData", JSON.stringify(data));
		localStorage.setItem("tableData", JSON.stringify(data1));

		/*  this.getConfig.then(val => {
		$.ajax({
		type: "GET",
		url: val.dataSources[modelName].uri,
		contentType: "application/json; charset=utf-8",
		timeout: 4000,
		dataType: "json",
		cache: true,
		data: params,
		success: function(response) {
		localStorage.setItem(storageID, JSON.stringify(response));
		},
		error: function(err) {
		console.info(JSON.stringify(err));
		}
		});
		});*/
	}
	getData(storageID) {
		return JSON.parse(localStorage.getItem(storageID)).Rowsets.Rowset[0].Row;
	}
}
new WIPDash().requestData();