import React, { Component } from "react";
import "./App.scss";
import { CanvasJSChart, Table } from "./canvasjs.react";
import WIPDash from "./Main.controller";

class Home extends Component {
    constructor(props) {
        super();
        this.state = {
            urlhistory: "",
            oModel: new WIPDash().getData("tableData"),
            chartData: new WIPDash().getData("tableData").map((e, i) => {
                return {
                    label: e.STATION_DESC,
                    y: e["En cours"],
                    x: i
                };
            })
        };
    }
    componentDidUpdate() {
        // this.props.history.replace("/home", "urlhistory");
    }

    render() {
        const arr = this.state.chartData,
            options = {
                animationEnabled: true,
                exportEnabled: true,
                theme: "light2",
                height: 795,
                width: 1775,
                title: {
                    text: "PCs",
                    fontWeight: "bold",
                    fontColor: "#008B8B",
                    fontfamily: "Source Sans Pro"
                },
                data: [
                    {
                        type: "column",
                        showInLegend: false,
                        legendText: "Station Description",
                        dataPoints: arr
                    }
                ]
            },
            //table
            rows = [],
            headings = [];
        this.state.oModel.forEach((e, i) => {
            rows.push(e);
            if (i === 0) {
                const head = Object.keys(e);
                head.forEach(e => {
                    headings.push({
                        data: e,
                        title: e,
                        width: 120
                        
                    });
                });
            }
        });

        return (
            <div className="Home">
                <div
                    className="tab-pane fade show active"
                    id="nav-home"
                    role="tabpanel"
                    aria-labelledby="nav-home-tab"
                >
                    <div id="tables">
                        <Table
                            ref="main"
                            id="mainTable"
                            data={rows}
                            columns={headings}
                            className="table table-striped hover"
                        />
                    </div>
                    <div id="graphs">
                        <CanvasJSChart options={options} />
                    </div>
                </div>
            </div>
        );
    }
}

export default Home;
