import React, { Component } from "react";
import "./App.scss";

class Info extends Component {
    constructor(props) {
        super();
        this.state = {
            urlhistory: ""
        };
    }
    componentDidUpdate() {
        // this.props.history.replace("/info", "urlhistory");
    }
    render() {
        return (
            <div
                className="tab-pane fade"
                id="nav-info"
                role="tabpanel"
                aria-labelledby="nav-info-tab"
            >
                {" "}
                <div>
                    <strong>
                        Currently displaying data from:{" "}
                        <mark>{localStorage.CurrentStation} Station</mark>
                    </strong>
                </div>
            </div>
        );
    }
}

export default Info;
