
let 
// eslint-disable-next-line
timeOut, 
lastTime;
export default class DeltaTimer {
	constructor(render, interval) {
		this.start = DeltaTimer.prototype.start;
		this.loop = DeltaTimer.prototype.loop.bind(this, interval, render);
		//	this.timeoutID = timeout;
	}
	start() {
		timeOut = setTimeout(this.loop, 0);
		lastTime = Date.now();
		return lastTime;
	}
	loop(interval, render) {
		const thisTime = Date.now(),
			deltaTime = thisTime - lastTime,
			delay = Math.max(interval - deltaTime, 0);

		timeOut = setTimeout(this.loop, delay);
		lastTime = thisTime + delay;
		render(thisTime);
	}
}